@import url(https://fonts.googleapis.com/css2?family=Maven+Pro&display=swap);
html,
body,
#root {
  margin: 0;
  height: 100%;
  font-family: "Maven Pro", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.App {
  display: grid;
  grid-template-columns: 20vw 1fr;
  height: 100%;
  box-sizing: border-box;
}

.App .sidebar,
.App form {
  display: grid;
  grid-template-rows: 1fr 200px;
  grid-template-columns: 1fr;
}

.sidebar {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: rgb(199, 237, 255);
}

.formlist {
  background-color: rgb(242, 251, 255);
  display: block;
  margin: 10%;
  font-weight: bold;
}

li ul {
  font-weight: normal;
}

ul {
  list-style-type: none;
}

li {
  margin: 1rem;
}

.canrender,
.cannotrender {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.cannotrender {
  background-color: rgb(227, 227, 227);
}

.cannotrender p {
  color: rgb(130, 130, 130);
}

.canrender button {
  border: none;
  cursor: pointer;
  font-weight: bold;
  font-size: 2.5rem;
  color: rgb(255, 255, 255);
  background-color: rgb(6, 0, 194);
  width: 100%;
  height: 100%;
  transition: 100ms all linear;
}

.canrender button:hover {
  border: 3px solid rgb(255, 255, 255);
  box-shadow: inset 0px 0px 27px 11px rgba(255, 255, 255, 0.5);
}

.cv {
  padding: 2% 5%;
}

.cvheader {
  display: grid;
  grid-template-areas:
    "user contact"
    "profession contact";
  grid-template-rows: -webkit-min-content;
  grid-template-rows: min-content;
  grid-template-columns: 4fr 1fr;
}

.cvname {
  grid-area: user;
  font-size: 3rem;
  margin: 0;
}

.cvprofession {
  grid-area: profession;
}

.cvcontact {
  grid-area: contact;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

table,
caption {
  width: 100%;
}

th,
td {
  padding: 10px;
}

.cvestablishment,
.cvinstitution {
  font-size: 1.5rem;
  text-decoration: underline;
}

.cvposition,
.cvqualification {
  font-size: 1.3rem;
}

.cvresponsibilities,
.cvsubject {
  font-style: italic;
}

th {
  text-align: left;
}

.cvdates {
  width: 20%;
  text-align: right;
}

caption {
  padding: 5px 20px;
  margin: 15px 0;
  font-size: 1.2rem;
  background: rgb(186, 186, 186);
  background: linear-gradient(
    90deg,
    rgba(186, 186, 186, 1) 0%,
    rgba(186, 186, 186, 1) 25%,
    rgba(255, 255, 255, 1) 84%
  );
  text-align: start;
  font-weight: bold;
  color: white;
}

.buttonContainer {
  background-color: rgb(199, 237, 255);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.buttonContainer button {
  outline: none;
  cursor: pointer;
  border: 2px solid rgb(255, 255, 255);
  border-radius: 10px;
  color: blue;
  font-size: larger;
  padding: 1.5rem;
  background-color: rgb(255, 255, 255);
  margin: auto 3rem;
  transition: all 100ms linear;
}

.backbutton:first-child {
  margin-right: auto;
}

.buttonContainer button:hover {
  border: 2px solid rgb(6, 0, 194);
  box-shadow: 0px 8px 8px 0px rgba(0, 0, 0, 0.1);
}

.buttonContainer button:active {
  border: 2px solid rgb(6, 0, 194);
  background-color: rgb(6, 0, 194);
  color: rgb(255, 255, 255);
  box-shadow: 0px 8px 8px 0px rgba(0, 0, 0, 0.1);
}

form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: rgb(255, 255, 255);
}

h1 {
  font-size: 2.5rem;
  color: rgb(4, 0, 133);
}

label {
  font-size: 90%;
  margin-bottom: 0.5rem;
}

input {
  border: 1px solid rgb(0, 0, 0);
  margin-bottom: 1rem;
  padding: 0.5rem;
}

input:focus {
  outline: 2px solid rgb(6, 0, 194);
}

.inputFields {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 5% 10%;
}

